:root {
    /* colors */
    --dark-grey: #24232C;
    --grey: #817D92;
    --almost-white: #E6E5EA;
    --very-dark-grey: #18171F;
    --neon-green: #A4FFAF;
    --red: #F64A4A;
    --orange: #FB7C58;
    --yellow: #F8CD65;
}

* {
    transition: all .1s;
}

body {
    font-family: 'JetBrains Mono', sans-serif;
    color: var(--almost-white);
    background: linear-gradient(180deg, #14131B 0%, #08070B 100%);
    margin: 0;
}

.hidden {
    clip: rect(0 0 0 0);
    clip-path: inset(50%);
    height: 1px;
    overflow: hidden;
    position: absolute;
    white-space: nowrap;
    width: 1px;
}

.container {
    min-width: 320px;
    max-width: 343px;
    padding-left: 16px;
    padding-right: 16px;
    margin-left: auto;
    margin-right: auto;
}

.section-generator {
    padding-top: 64px;
    padding-bottom: 63px;
}

.title {
    margin-bottom: 16px;
    color: var(--grey);
    text-align: center;
    display: flex;
    padding-left: 90px;

}

.app-name {
    margin-top: 0px;
    margin-bottom: 0px;
    margin-right: 40px;
    font-size: 16px;
    font-weight: 700;

}

.password {
    background-color: var(--dark-grey);
    padding: 17px 16px 15px 16px;
    display: flex;

    margin-bottom: 16px;
}

.text-password {
    margin-top: 0;
    margin-bottom: 0;
    margin-right: 100px;
    font-size: 24px;
    font-weight: 700;

}

.copied {
    color: var(--neon-green);
}

.btn-copy {
    height: 20px;
}

.generator-form {
    background-color: var(--dark-grey);
    padding: 16px;
}

.selected-length {
    display: flex;
    gap: 128px;
    align-items: center;
    margin-bottom: 23px;
}

.text-length {
    margin-top: 0px;
    margin-bottom: 0px;
    font-weight: 700;
}

.number-length {
    font-size: 24px;
    font-weight: 700;
    color: var(--neon-green);
}


.input-length {
    appearance: none;
    width: 100%;
    height: 8px;
    background: var(--very-dark-grey);
    background-image: linear-gradient(var(--neon-green), var(--neon-green));
    background-size: 0% 100%;
    background-repeat: no-repeat;
    margin-bottom: 42px;
}

[dir="rtl"] .input-length {
    background: var(--neon-green);
    background-image: linear-gradient(#fff, #fff);
    background-size: 30% 100%;
    background-repeat: no-repeat;
}

/* Input Thumb */
.input-length::-webkit-slider-thumb {
    appearance: none;
    height: 28px;
    width: 28px;
    border-radius: 50%;
    background: var(--almost-white);
    cursor: pointer;
    transition: background .3s ease-in-out;
}

.input-length::-moz-range-thumb {
    appearance: none;
    height: 28px;
    width: 28px;
    border-radius: 50%;
    background: var(--almost-white);
    cursor: pointer;
    transition: background .3s ease-in-out;
}

.input-length::-ms-thumb {
    appearance: none;
    height: 28px;
    width: 28px;
    border-radius: 50%;
    background: var(--almost-white);
    cursor: pointer;
    transition: background .3s ease-in-out;
}

.input-length::-webkit-slider-thumb:hover {
    background: var(--dark-grey);
    border: 2px solid var(--neon-green);
}

.input-length::-moz-range-thumb:hover {
    background: var(--dark-grey);
    border: 2px solid var(--neon-green);
}

.input-length::-ms-thumb:hover {
    background: var(--dark-grey);
    border: 2px solid var(--neon-green);
}

/* Input Track */
.input-length::-webkit-slider-runnable-track {
    appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
}

.input-length::-moz-range-track {
    appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
}

::-ms-track {
    appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
}



.checkboxes {
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin-bottom: 32px;

}

.checkbox {
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    gap: 20px;
    font-weight: 700;
}



.password-strength {
    display: flex;
    width: 279px;
    background-color: var(--very-dark-grey);
    gap: 57px;
    padding: 14px 16px;
    margin-bottom: 16px;
}

.text-strength {
    margin: 0px;
    color: var(--grey);
}

.actual-strength {
    display: flex;
    gap: 16px;
}

.strength-value {
    font-size: 18px;
}


.strength-level {
    display: flex;
    gap: 8px;
}

.strength {
    width: 6px;
    height: 24px;
    border: 2px solid var(--almost-white);
}

.too-weak {}

.weak {}

.medium {}

.strong {}


.btn-generate {

    display: block;
    width: 100%;
    height: 56px;
    background-color: var(--neon-green);
    color: var(--dark-grey);
    cursor: pointer;
    font-weight: 700;
    letter-spacing: 0.050em;
    border: none;

}

.btn-generate:hover {
    background-color: transparent;
    border: 2px solid var(--Neon-Green, #A4FFAF);
    color: var(--neon-green);
}

.btn-generate:focus {
    background-color: transparent;
    border: 2px solid var(--Neon-Green, #A4FFAF);
    color: var(--neon-green);
}


.slider-btn {
    position: relative;

}

.slider-btn::before {
    bottom: 0;
    content: '';
    display: inline-block;
    margin: auto;
    background-color: var(--grey);
    border-radius: 50px;
    top: 0;
    height: 15px;
    width: 30px;
    box-shadow: 0 0 4px rgba(0, 0, 0, 0.493);
}


/* circle */
.slider-btn::after {
    bottom: 0;
    content: '';
    display: inline-block;
    margin: auto;
    background-color: var(--neon-green);
    border-radius: 50%;
    position: absolute;
    top: -5px;
    left: 5px;
    height: 10px;

    transition: .4s;
    width: 10px;
}


.toggle-checkbox {
    /* hide the "default" checkbox/appearance */
    display: none;
}

.toggle-checkbox:checked~.slider-btn:before {
    background-color: var(--almost-white);
}

.toggle-checkbox:checked~.slider-btn:after {
    background-color: maroon;
    transform: translateX(10px);
}

